<template>
  <div >
      <el-row  type="flex" justify="center">
        <el-col :span="23">
          <el-tabs v-model="navActive" @tab-click="handleClick">
            <el-tab-pane label="按专员维度查看" name="agentData">

            </el-tab-pane>
            <el-tab-pane label="按办会人维度查看" name="customerData">

            </el-tab-pane>
            <el-tab-pane label="按字段维度查看" name="fieldData">

            </el-tab-pane>
          </el-tabs>
          <router-view @changeNav="changeActiveNav"></router-view>
        </el-col>
      </el-row>
  </div>
</template>

<script>

// import UserList from '../components/index/UserList.vue'
// import ContentWrap from '../components/index/ContentWrap.vue'
export default {
  name: 'mngview',
  data: function () {
    return {
        navActive: 'agentData'
    }
  },
  mounted() {

  },
  methods: {
    handleClick(tab, event) {
      var tabName = tab.name;
      var self = this;
      self.$router.push({'path':'/foreground/mngview/'+tabName})
    },
    changeActiveNav() {
      this.navActive = 'fieldData'
    }
  },

  components:{
    // UserList,
    // ContentWrap
  }
}
</script>
<style lang="less">

</style>



